/* VIDEOS size small weirdly */

body {
	margin: 100px;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-image: url("/Users/Fin.co/Desktop/scona-live-2/src/Untitled.jpg");
	background-size: cover;
	background-attachment: fixed;
	background-color: #F1C548;
}

@media only screen and (max-width: 760px) {
	body{
		margin: 25px;
		background-image: none;
	}
}

.notSelectable{
	user-select: none;
	-moz-user-select: none;
	-webkit-user-drag: none;
	-webkit-user-select: none;
	-ms-user-select: none;
}


::-webkit-scrollbar{
	width: 12px;
}

::-webkit-scrollbar-track{
	background-color: white;
	margin-top: -5px;
	margin-bottom: -5px;
}

::-webkit-scrollbar-thumb{
	background-color: #D23943;
	border-radius: 5px;
}

.row{
	display: flex;
	flex-direction: row;
	width: 100%;
	margin-bottom: 50px;
	justify-content: space-between;


/* TODO get the gap between constant (make waring exppand more) */
	/* display: grid;
	grid-template-columns: repeat(auto-fit, minmax(500px, 1fr));
	grid-gap: 50px;
	margin-bottom: 50px; */
}



.titleContainer{

	display: flex;
	flex-direction: column;
	background-color: white;
	border-radius: 10px;
	box-shadow: 0 0 20px 16px #0000001f;
}


.main{
	width: 100%;
	padding-top: 50px;
	padding-bottom: 50px;
	margin-bottom: 50px;
}

.note{


	/* max-height: 146px; */
	margin-left: 50px;
	height: auto;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	/* padding: 20px; */
}


.note p{
	margin: 20px;
}

.title{
	width:max-content;
	text-align: center;
	/* padding-left: 10px; */
    color: #D23943;
    font-size: 5em;
	margin: 25px;
}

.mainImg{
	margin-right: 50px;
	-webkit-filter: drop-shadow(0px 0px 16px #0000001f);
	filter: drop-shadow(0 0 16px rgba(0, 0, 0, 0.55));
	border-radius: 10px;
}

.smallLogo{
	max-width: 350px;
}

@media only screen and (max-width: 1230px) {
	.titleContainer{ margin-bottom: 50px;}
	.title{width:auto;}
	/* .delete{background-color: transparent; box-shadow: none;} */
	.note{ margin: 0;}
	.row {
		flex-direction: column;
		align-items: center;
	}
	.mainImg{
		display: none;
	}
}

@media only screen and (max-width: 599px) {
	.title{margin-top: 6px;}
}



.subtitle{
	color: #D23943;
	font-size: 3em;
	width: 85%;
	text-align: center;
	margin-bottom: 0;
	margin-left: auto;
	margin-right: auto;
}

.tinyTitle{
	font-weight: bold;
	width: 100%;
	text-align: center;
}


.archived{
	/* border: 2px solid black; */
	border-radius: 10px;
	/* margin-top: 150px; */
	/* margin-bottom: 160px; */
	width: 75%;
	margin-left: auto;
	margin-right: auto;
	padding-bottom: 60px;
	padding-top: 20px;
	padding-right: 25px;
	padding-left: 25px;
	max-width: 900px;
	background-color: white;
}


.column{
	width: 100%;
	height: auto;
	display: flex;
	flex-direction: column;
	/* align-items: center;
	justify-content: space-between; */
}

.columnEl{
	-webkit-box-sizing: border-box; 
	-moz-box-sizing: border-box;    
	box-sizing: border-box; 

	background-color: white;
	width: 100%;
	border-radius: 10px;

	padding: 25px;

	margin-bottom: 50px;
	box-shadow: 0 0 20px 16px #0000001f;
	display: flex;
	flex-direction: column;
}

.columnEl:nth-child(2){
	margin-bottom: 0;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.live{
	display: inline-flex;
	align-items: center;
	color: white;
	background-color: #D23943;
	padding-bottom: 2px;
	cursor: pointer;
	transition: transform 300ms;
	backface-visibility: hidden;
}

.live:hover{
	transform: scale(1.05);
}

.live:hover > .circle{
	animation: blink 1s steps(1, start) infinite;
}

.circle{
	height: 30px;
	width: 30px;
	background-color: white;
	border-radius: 50%;
	display: inline-block;
	margin-bottom: -1px;
}

.videoGrid{
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
	grid-gap: 50px;
	width: 85%;
	margin-left: auto;
	margin-right: auto;
	margin-top: 50px;
	margin-bottom: 50px;
}

@media only screen and (max-width: 570px) {
	.videoGrid {
		grid-template-columns: repeat(1, 100%);
	}
}

.videoWrapper{
	position: relative;
	padding-bottom: 56.25%;
}

.video{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.75);

	border-radius: 10px;

	/* The background color makes the squares visible even when the iFrame isn't loaded compeltely */
}

.horizontal{
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	width: 300px;
	margin-left: auto;
	margin-right: auto;
}


.lightText{
	text-align: center;
	width: 100%;
	margin-top: 50px;
	margin-bottom: 50px;
	opacity: 0.75;
}

a{
	/* transition: opacity 400ms; */
	opacity: 1;
}

a:hover{
	opacity: 0.6;
}

.footer{
	width: 100%;
	text-align: center;
	color:#F1C548;
	filter: brightness(70%);
	margin-top: 100px;
}

@keyframes blink{
	0%{ opacity: 1}
	50%{ opacity: 0; }
}

.popupContainer{
	position: fixed;
	left: 0;
	bottom: -300px;
	margin: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #D23943;
	padding: 20px;
	padding-left: 50px;
	padding-right: 50px;
	border-top: solid black 3px;
	transition: opacity 1.5s ease-in, bottom 2s, visibility 2s;
}

@media only screen and (max-width: 500px) {
	.popupContainer {
		padding: 10px;
	}
}

.xBtn{
    border: 0;
    background-color: transparent;
    font-size: 2.5em;
    font-family: Arial, Helvetica, sans-serif;
    z-index: 100;
    margin: 5px;
    margin-top: 15px;
    cursor: pointer;
    transform: rotate(45deg);
    transition: transform 1s;
    backface-visibility: hidden;
    margin-bottom: 10px;
}

.xBtn:hover{
    transform: rotate(135deg);
}


.cumming{
	/* background-color: #333333; */
	display: flex;
	flex-direction: column;
}

.mainContainer{
	position: relative;
	min-height: 85%;
	padding-bottom: 25px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: center;
	background-color: white;
	/* margin: 50px; */
	border-radius: 10px;
}


